import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import BreadCrumb from "../components/common/breadcrumb";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import Web3DevelopmentScreen from "../screens/web3-development";

const Web3SmartContractDevelopment = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Web3 Smart Contract Development Company", link: null },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <main>
        <Web3DevelopmentScreen db={db} />
      </main>
    </Layout>
  );
};

export const Head = () => (
  <Seo
    title="Web3 Smart Contract Development Company | Justtry Technologies"
    headTitle="Web3 Smart Contract Development Company | Justtry Technologies"
    description="Justtry Tech is a web3 smart contract development company that provides DeFi smart contract development services such as Binance smart contract development, Tron smart contract development, EOS smart contract development, and etc."
    keywords="Web3 Smart Contract Development, Blockchain Smart Contracts, Solidity Development Services, Ethereum Smart Contracts, Binance Smart Chain Development, Smart Contract Auditing, Tokenomics Services, DeFi Smart Contracts, NFT Smart Contracts, Decentralized Applications (DApps), Smart Contract Security, Custom Blockchain Solutions, Blockchain Protocols, Smart Contract Integration, Smart Contract Optimization, Token Development Services, Smart Contract Deployment, Smart Contract Consulting, Cross-Chain Development, Smart Contract Upgrades, Smart Contract Interoperability, Consensus Mechanisms, Blockchain Development Company, Decentralized Finance Development,
    Contract-as-a-Service (CaaS)"
  ></Seo>
);

export default Web3SmartContractDevelopment;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
